/* CARDS */

.profile-card-2 {
  width: 80%;
  background-color: #FFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
  background-position: center;
  overflow: hidden;
  position: relative;
  /* margin: 10px auto; */
  cursor: pointer;
  /* border-radius: 10px; */
}

.profile-card-2 img {
  transition: all linear 0.25s;
}

.profile-card-2 .profile-name {
  position: absolute;
  left: 30px;
  bottom: 70px;
  font-size:1.875rem;
  color: #FFF;
  text-shadow: 0px 0px 20px rgba(44, 44, 44, 0.5);
  font-weight: normal;
  transition: all linear 0.25s;
}

.profile-card-2 .comprarLink {
  position: absolute;
  bottom: 30px;
  right: 30px;
  color: white;
  transition: all linear 0.25s;
}

.profile-card-2 .profile-username {
  position: absolute;
  bottom: 50px;
  left: 30px;
  color: #FFF;
  font-size: 13px;
  transition: all linear 0.25s;
}



.profile-card-2 .comprarLinks {
  margin: 5px;
}

.profile-card-2:hover img {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.profile-card-2:hover .profile-name {
  bottom: 80px;
  
}

.profile-card-2:hover .profile-username {
  bottom: 60px;
}

.profile-card-2:hover .comprarLink {
  right: 40px;
}

.comprarLink {
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

.comprarLink:hover {
  text-decoration: none !important;
  color: #71668a !important;
  font-weight: bold;
}

.profileTexto {
